import { Component, OnInit } from '@angular/core'; import { Router, ActivatedRoute } from '@angular/router'; import { Subscription } from 'rxjs/Subscription';  import { Fluxograma } from '../../../modelo/Fluxograma'; import { ObjetoVirtual } from '../../../modelo/ObjetoVirtual'; import { PesquisaService } from '../../../core/services/pesquisa/pesquisa.service';  @Component({   moduleId: module.id,   selector: 'fluxograma-dados-details',   templateUrl: 'fluxograma_dadosDetails.component.html' }) export class FluxogramaDadosDetailsComponent implements OnInit {    fluxograma: Fluxograma;    private sub: Subscription;    private errorMessage: string;    constructor(private route: ActivatedRoute, private pesquisaService: PesquisaService) { }    ngOnInit() {       this.sub = this.route.parent.params.subscribe(params => {         let o : ObjetoVirtual = new ObjetoVirtual();         o.classeJava = "br.com.primum.modelo.fluxograma.Fluxograma";                 let id = +params['id'];         this.fluxograma = new Fluxograma();         this.fluxograma.cd_fluxograma = id;         o.objetoJson = this.fluxograma;          this.pesquisaService.efetuarPesquisa(o)             .subscribe(             result => this.onEfetuarPesquisa(result),             error =>  this.errorMessage = <any>error);       });   }    onEfetuarPesquisa(result : any) {     if (!result) {       this.errorMessage = 'Houve um erro ao pesquisa a fluxograma. '; }      this.fluxograma = result;   }    ngOnDestroy() {     this.sub.unsubscribe();   } }


